import React from "react"

import SuggestionComponent from "../components/pages/Suggestion"
import Layout from "../components/Layout"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const Career = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "suggestion",
			name: translate("breadCrumb.suggestion"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Sugestões, reclamações e Melhorias"
				description="Sugestões, reclamações e Melhorias"
			></SEO>
			<SuggestionComponent />
		</Layout>
	)
}

export default Career
