import React from "react"

import { Container } from "./styles"

import Banner from "../../utils/Banner"
import Intro from "../../utils/IntroductionContent"
import { translate } from "../../utils/translate"
import FormSuggestion from "../../utils/Form/FormSuggestion"

export default function Suggestion() {
	return (
		<div>
			<Banner titlePage={translate("suggestion.headLine")} />
			<Container className="container">
				<h2>{translate("suggestion.introduction")}</h2>
			</Container>
			<FormSuggestion />
		</div>
	)
}
